  position: fixed;
  top: 0;
  background-color: rgba(42, 3, 64, 0.75);
  height: 100%;
  width: 100%;
  transition: 0.4s ease;
  opacity: 0;
  visibility: hidden;
  z-index: 10;
}

.pantalla-mostrar-contenido {
  opacity: 1;
  visibility: visible;
}

.swal2-popup {
  font-size: 2rem !important;
}

.grecaptcha-badge {
  display: none !important;
}

