[class$=boton--morado] {
  background-color: #7509B3;
  padding: 2.3rem 8rem 2.3rem 6rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  border-radius: 4rem;
  transition: 0.4s ease;
  text-align: center;
  width: auto;
  display: inline-block;
  position: relative;
}
[class$=boton--morado] i {
  color: #FFFFFF;
  font-size: 3rem;
  position: absolute;
  top: 50%;
  right: 4rem;
  transform: translateY(-50%);
}
[class$=boton--morado]:hover {
  background-color: #360452;
  cursor: pointer;
}

[class$=boton--naranja] {
  background-color: #FFA500;
  padding: 2.3rem 8rem 2.3rem 6rem;
  color: #1b1b1b;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  border-radius: 4rem;
  transition: 0.4s ease;
  text-align: center;
  width: auto;
  display: inline-block;
  position: relative;
}
[class$=boton--naranja] i {
  color: #1b1b1b;
  font-size: 3rem;
  position: absolute;
  top: 50%;
  right: 4rem;
  transform: translateY(-50%);
}
[class$=boton--naranja]:hover {
  background-color: #996300;
  cursor: pointer;
}
[class$=boton--naranja]:hover {
  background-color: #ffb733;
}

[class$=boton--rojo] {
  background-color: #FF4136;
  padding: 2.3rem 8rem 2.3rem 6rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  border-radius: 4rem;
  transition: 0.4s ease;
  text-align: center;
  width: auto;
