.inicio {
  min-height: 50rem;
  height: 100vh;
  max-height: 120rem;
  background: url(../img/rayas-fondo-textura.svg) center center/cover no-repeat, #FFA500;
  background-size: 200% 200%;
  width: 100%;
}
.inicio__contenedor {
  height: 100%;
  display: flex;
  align-items: center;
  margin: auto;
  position: relative;
}
.inicio__contenido {
  display: flex;
  align-items: baseline;
  flex-direction: column;
}
@media (min-width: 768px) {
  .inicio__contenido {
    width: 60%;
  }
}
.inicio__titulo {
  font-size: clamp(2rem, -0.8rem + 10vw, 2.5vw);
}
@media (min-width: 768px) {
  .inicio__titulo {
    white-space: nowrap;
  }
}
.inicio__titulo--resaltar {
  font-size: clamp(4rem, -0.8rem + 15vw, 8rem);
  color: #7509B3;
  display: block;
}
.inicio__imagen-contenedor {
  display: none;
}
@media (min-width: 768px) {
  .inicio__imagen-contenedor {
    display: block;
    position: absolute;
    bottom: 0;
    right: -5%;
    width: 55%;
    height: calc(100% - 10rem);
  }
}
@media (min-width: 768px) {
  .inicio__imagen-contenedor img {
    position: absolute;
    bottom: 0;
    width: auto;
    width: 100%;
    max-height: 53rem;
  }
}

.servicios {
  background-color: #F9F9F9;
}
.servicios__seccion {
  margin-top: 0;
  padding: 10rem 0;
}
.servicios__titulo {
  text-align: center;
}
.servicios__titulo--resaltar {
  color: #FFA500;
}
.servicios__sitios-web {
  margin-top: 12rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 12rem;
}
@media (min-width: 768px) {
  .servicios__sitios-web {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 12rem;
  }
}
@media (min-width: 900px) {
  .servicios__sitios-web {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 2rem;
  }
}
.servicios__opcion {
  position: relative;
}
.servicios__numero {
  font-size: 15rem;
  position: absolute;
  z-index: 1;
  top: -14rem;
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-weight: 800;
  color: #7509B3;
  left: 50%;
  transform: translateX(-50%);
  pointer-events: none;
  user-select: none;
}
.servicios__contenido {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.55);
  backdrop-filter: blur(8px);
  padding: 2rem 1rem;
  border-radius: 0.5rem;
  position: relative;
  z-index: 10;
  box-shadow: 0 8px 24px rgba(149, 157, 165, 0.2);
  transition: 0.4s ease;
  transform: translateY(0);
}
.servicios__contenido:hover {
  transform: translateY(-5rem);
}
.servicios__nombre {
  font-family: "Mulish", sans-serif;
  text-align: center;
  font-size: 2.2rem;
  font-weight: 800;
}
.servicios__caracteristicas {
  list-style-type: none;
  padding-left: 0;
  margin: 1rem auto 1rem 0;
}
.servicios__descripcion {
  position: relative;
  padding-left: 2rem; /* Espacio para la línea vertical */
  margin-bottom: 0.5rem; /* Espacio entre elementos de la lista */
  left: -1rem;
}
.servicios__descripcion:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 1rem; /* Ancho de la línea vertical */
  background-color: #FFA500; /* Color de la línea vertical */
}
.servicios__descripcion--modificador::before {
  background-color: #7509B3; /* Color de la línea vertical */
}
.servicios__boton--naranja {
  white-space: nowrap;
}

