.preguntas {
  padding: 10rem 0;
  background-color: #F9F9F9;
}
.preguntas__titulos {
  text-align: center;
}
.preguntas__titulos--resaltar {
  color: #FFA500;
}
.preguntas__frecuentes {
  display: grid;
  gap: 2rem;
  margin-top: 3rem;
}
.preguntas__acordeon {
  position: relative;
  width: 100%;
  background-color: #FFFFFF;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  transition: 0.4s ease;
  overflow: hidden;
}
.preguntas__acordeon:nth-child(even) {
  margin: 0;
}
@media (min-width: 480px) {
  .preguntas__acordeon {
    width: 60%;
  }
  .preguntas__acordeon:nth-child(even) {
    margin-right: 0;
    margin-left: auto;
  }
}
.preguntas__acordeon--observar .preguntas__linea {
  transform: rotate(0) translateX(6px);
}
.preguntas__acordeon--observar .preguntas__linea:nth-child(2) {
  transform: none;
}
.preguntas__duda {
  font-family: "Mulish", sans-serif;
  font-size: 2.2rem;
  text-transform: initial;
}
.preguntas__vista {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
