  display: flex;
  justify-content: center;
  align-items: center;
}
.preguntas__linea {
  width: 2rem;
  height: 3px;
  background-color: #7509B3;
  transform: rotate(45deg);
  border-radius: 3px;
  transition: 0.4s ease;
}
.preguntas__linea:nth-child(2) {
  transform: rotate(315deg) translate(-5px, -5px);
}
.preguntas__altura {
  margin: 0;
  max-height: 0;
  transition: 0.4s ease;
}
.preguntas__respuesta {
  margin-top: 0;
}

