.navegacion__enlace {
  color: #1b1b1b;
  transition: 0.4s ease;
  font-size: 2.5rem;
  font-weight: 700;
  padding: 1rem;
  position: relative;
  width: 100%;
  height: auto;
  z-index: 1;
  border-radius: 5rem;
  overflow: hidden;
}
.navegacion__enlace::after, .navegacion__enlace::before {
  content: "";
  position: absolute;
  top: 0;
  transition: 0.4s ease;
  width: 0%;
  height: 100%;
  background-color: #FFA500;
  z-index: -1;
}
.navegacion__enlace::after {
  left: 0;
}
.navegacion__enlace::before {
  right: 0;
}
.navegacion__enlace:hover::after, .navegacion__enlace:hover::before {
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    all: unset;
    padding: 1rem;
    color: #1b1b1b;
    font-size: 1.8rem;
    cursor: pointer;
    transition: 0.4s ease;
    position: relative;
    display: grid;
    place-content: center;
  }
  .navegacion__enlace::after, .navegacion__enlace::before {
    all: unset;
  }
  .navegacion__enlace::after {
    content: "";
    position: absolute;
    background-color: #7509B3;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: 0.4s ease;
  }
  .navegacion__enlace:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
}
.navegacion .menu-toggle__span {
  background-color: #1b1b1b;
  border-radius: 2px;
  display: block;
  transition: 0.35s ease-in-out;
}
.navegacion #menu-toggle {
  border-radius: 5px;
  cursor: pointer;
  height: 10rem;
  position: relative;
  width: 10rem;
  transform: scale(0.8) translateX(20%);
}
.navegacion #menu-toggle .menu-toggle__hamburger {
  height: 100%;
  position: absolute;
  width: 100%;
}
.navegacion #menu-toggle .menu-toggle__hamburger .menu-toggle__span {
  height: 4px;
  left: 2rem;
  margin: 1rem 0;
  position: relative;
  top: 2.4rem;
  width: 6rem;
}
.navegacion #menu-toggle .menu-toggle__hamburger .menu-toggle__span:first-child {
  transition-delay: 0.5s;
}
.navegacion #menu-toggle .menu-toggle__hamburger .menu-toggle__span:nth-child(2) {
  transition-delay: 0.625s;
}
.navegacion #menu-toggle .menu-toggle__hamburger .menu-toggle__span:nth-child(3) {
  transition-delay: 0.75s;
}
.navegacion #menu-toggle .menu-toggle__cross {
  height: 100%;
  position: absolute;
  transform: rotate(45deg);
  width: 100%;
}
.navegacion #menu-toggle .menu-toggle__cross .menu-toggle__span:first-child {
  height: 0;
  left: 4.8rem;
  position: absolute;
  top: 10%;
  transition-delay: 0s;
  width: 4px;
}
.navegacion #menu-toggle .menu-toggle__cross .menu-toggle__span:nth-child(2) {
  height: 4px;
  left: 10%;
  position: absolute;
  top: 4.8rem;
  transition-delay: 0.25s;
  width: 0;
}
.navegacion #menu-toggle.open .menu-toggle__hamburger .menu-toggle__span {
  width: 0;
}
.navegacion #menu-toggle.open .menu-toggle__hamburger .menu-toggle__span:first-child {
  transition-delay: 0s;
}
.navegacion #menu-toggle.open .menu-toggle__hamburger .menu-toggle__span:nth-child(2) {
  transition-delay: 0.125s;
}
.navegacion #menu-toggle.open .menu-toggle__hamburger .menu-toggle__span:nth-child(3) {
  transition-delay: 0.25s;
}
.navegacion #menu-toggle.open .menu-toggle__cross .menu-toggle__span:first-child {
  height: 80%;
  transition-delay: 0.625s;
}
.navegacion #menu-toggle.open .menu-toggle__cross .menu-toggle__span:nth-child(2) {
  transition-delay: 0.375s;
  width: 80%;
}
@media (min-width: 768px) {
  .navegacion #menu-toggle {
    display: none;
  }
}

