.formulario__input::placeholder, .formulario__textarea::placeholder {
  color: #353535;
}
.formulario__input:focus, .formulario__textarea:focus {
  border-color: #7509B3;
}
.formulario__input--terminos {
  accent-color: #7509B3;
}
.formulario__textarea {
  height: 15rem;
}
.formulario__terminos {
  display: block;
  margin-bottom: 4rem;
}
.formulario__enlace--terminos, .formulario__enlace-olvide {
  color: #7509B3;
  transition: 0.4s ease;
}
.formulario__enlace--terminos:hover, .formulario__enlace-olvide:hover {
  color: #360452;
  text-decoration: underline;
}
.formulario__enlace-olvide {
  margin: 2rem 0 4rem;
  display: block;
}
.formulario__error, .formulario__error-validacion {
  padding: 0.5rem 1rem 0.5rem 3rem;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 1;
  background-color: #ffd2cf;
  border-left: 0.5rem solid #FF4136;
  color: #FF4136;
  display: none;
  font-size: 1.2rem;
}
.formulario__error i, .formulario__error-validacion i {
  margin-right: 0.5rem;
  font-size: 1.6rem;
}
.formulario__error-validacion {
  display: block;
}

.footer {
  background-color: #1b1b1b;
  color: #FFFFFF;
  display: grid;
  place-content: center;
  height: 7rem;
  width: 100%;
}
.footer p {
  text-align: center;
}
.footer span {
  font-weight: 700;
}